input[type="submit"] {
  display: block;
  border: 2px solid #CCC;
  background: #333;
  color: #FFF;
  padding: 0.6em 2.2em;
  border-radius: 0.4em;
  font-weight: bold;
  cursor: pointer; }

input[type="submit"]:hover {
  background: #060;
  -webkit-transition: background 0.3s ease, border-color 0.3s ease;
  transition: background 0.3s ease, border-color 0.3s ease; }

