#main-nav-toggle,
#toggle {
  display: none; }

#toggle {
  position: relative;
  -webkit-box-flex: 1;
  -ms-flex: 1 0 auto;
  flex: 1 0 auto;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-line-pack: center;
  align-content: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  padding: 1rem 0; }

#toggle i.fas {
  display: inline-block;
  margin-right: 0.5rem; }

#toggle i.fas.fa-times {
  display: none; }

@media screen and (max-width: 800px) {
  #toggle {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-flex: 0;
    -ms-flex: 0 0 auto;
    flex: 0 0 auto; }
  .navigation-main {
    display: none;
    border-top: 2px solid #000;
    margin-top: 1rem; }
  .navigation-main ul li a {
    text-align: center; }
  .navigation-main ul li:not(:first-child) {
    padding-top: 0.5rem; }
  #main-nav-toggle:checked + header nav.main {
    display: block;
    -webkit-box-flex: 1;
    -ms-flex: 1 0 100%;
    flex: 1 0 100%; }
  #main-nav-toggle:checked + header #toggle .fa-times {
    display: block; }
  #main-nav-toggle:checked + header #toggle .fa-bars {
    display: none; } }

@media screen and (min-width: 800px) {
  .navigation-main ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex; }
  .navigation-main ul li:hover {
    display: block; } }

